<template>
  <div class="content">
    <div class="topHeard">
      <img src="https://img.yzcdn.cn/vant/apple-1.jpg" />
    </div>
    <section class="formContent">
      <form action="#">
        <div class="formLine" @click="openPickerTime">
          <p>开始时间</p>
          <p>{{currentDateTime}}</p>
        </div>
        <div class="formLine" @click="openPickerTime">
          <p>结束时间</p>
          <p>{{currentDateTime}}</p>
        </div>
        <div class="formLine">
          <p>员工工号</p>
          <input type="text" name id placeholder="请输入员工工号"/>
        </div>
        <div class="formLine">
          <p>会员手机号</p>
          <input type="text" name id placeholder="请输入会员手机号"/>
        </div>
        <div class="formLine">
          <p>账单号</p>
          <input type="text" name id placeholder="请输入账单号"/>
        </div>
      </form>
      <div class="submit" @click="$router.push({path:'/orderItem'})">确认查询</div>
    </section>
    <section class="pickerDataTime">
      <van-overlay :show="show" @click="openPickerActive = false ; show = false" />
      <van-datetime-picker
        v-model="currentDate"
        type="datetime"
        :min-date="minDate"
        :max-date="maxDate"
        v-show="openPickerActive"
        @cancel="openPickerActive = false ; show = false"
        @confirm="confirmTime"
      />
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      openPickerActive: false,
      minDate: new Date(),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date(),
      currentDateTime: this.util.getTime(new Date()),
      show: false
    };
  },
  methods: {
    openPickerTime() {
      this.openPickerActive = !this.openPickerActive;
      this.show = !this.show;
    },
    confirmTime(value) {
      this.currentDateTime = this.util.getTime(value);
      this.openPickerActive = !this.openPickerActive;
      this.show = !this.show;
    }
  }
};
</script>

<style lang="scss" scoped>
.content {
  .topHeard {
    padding: 0.8rem 0.5rem;
    margin: 0.5rem 0.8rem;
    img {
      @include wh(100%, 8rem);
      border-radius: 0.5rem;
    }
  }
  .formContent {
    background-color: #fff;
    box-shadow: 0px 0.3rem 0.8rem $Tline;
    border-radius: 0.5rem;
    padding: 0.5rem;
    margin: 0.5rem 0.8rem;
    .formLine {
      @include fr(space-between);
      padding: 0.5rem;
      margin: 0.5rem;
      border-bottom: 0.05rem solid $F5;
    }
    input{
      text-align: right;
    }
  }
  .pickerDataTime {
    position: fixed;
    bottom: 0;
    z-index: 2;
    @include wh(100%, auto);
  }
}
/deep/ .van-datetime-picker{
  z-index: 2;
}
</style>